.gobopening {
  margin-top: 76px;
  padding-top: 150px;
  padding-bottom: 100px;
}
.gobopening h2 {
  font-size: 48px;
  font-weight: 400;
  color: #4d4d4d;
  line-height: 48px;
}
.gobopening h3 {
  font-size: 26px;
  font-weight: 400;
  color: #303030;
  line-height: 26px;
  margin-top: 20px;
}
.gobopening ul {
  margin-top: 35px;
}
.gobopening ul li {
  font-size: 15px;
  font-weight: 400;
  color: #666666;
  line-height: 31px;
}
.engineering {
  padding-bottom: 100px;
}
.engineering .project .prolist {
  display: none;
}
.engineering .title {
  position: relative;
  margin: 30px 0;
  padding-bottom: 30px;
  border-bottom: 1px solid #E8EAED;
}
.engineering .title h3 {
  color: #4A4A4A;
  display: inline-block;
  cursor: pointer;
  font-size: 30px;
  font-weight: 400;
}
.engineering .title .opening {
  position: absolute;
  font-size: 16px;
  color: #666666;
  right: 0;
  top: 4px;
  padding-right: 20px;
  cursor: pointer;
}
.engineering .title .opening::after {
  content: "";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  top: 8px;
  right: 0;
  border: 1px solid #8E8E8E;
  border-top: transparent;
  border-left: transparent;
  transform: rotate(45deg);
  transition-duration: 0.3s;
}
.engineering .gtil {
  margin-left: 45px;
  font-size: 20px;
  color: #4A4A4A;
  margin-bottom: 30px;
}
.engineering .gtil:hover {
  color: #496DEC;
  cursor: pointer;
}
.engineering .glist {
  margin-left: 45px;
  border-left: 1px solid #E8EAED;
  padding-left: 20px;
  margin-bottom: 35px;
  display: none;
}
.engineering .glist .list {
  color: #4A4A4A;
}
.engineering .glist .list label {
  display: block;
  font-size: 15px;
  font-weight: 800;
  margin-bottom: 10px;
}
.engineering .glist .list ul {
  margin-bottom: 20px;
}
.engineering .glist .list li {
  font-size: 15px;
  line-height: 30px;
  position: relative;
  margin-left: 10px;
}
.engineering .glist .list li::after {
  content: "";
  display: block;
  position: absolute;
  width: 2px;
  height: 2px;
  background-color: #4A4A4A;
  border-radius: 50%;
  left: -8px;
  top: 15px;
}
.engineering .glist .sendaddr {
  color: #496DEC;
  font-size: 15px;
}
.engineering .endactive .gtil {
  color: #496DEC;
}
.engineering .endactive .glist {
  display: block;
}
.engineering .proactive .prolist {
  display: block;
}
.engineering .proactive .opening::after {
  transform: rotate(-135deg);
  top: 10px;
}
.blkcent {
  box-sizing: border-box;
  padding: 0 20px;
}
